@import url('https://fonts.googleapis.com/css?family=Skranji:700');
@import url('https://fonts.googleapis.com/css?family=Orbitron');

body{
  background-color:#053d49;
    color:white;
    text-align:center;
    font-family: 'Skranji', cursive;

}
.container{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height:100vh;
  width:100vw;
  -ms-flex-align:baseline;
      align-items:baseline;
  -ms-flex-pack:distribute;
      justify-content:space-around;
}
  .title{
    font-size:8vh;
    margin:auto;
  }
  .controls{
  margin:auto;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-pack:start;
      justify-content:flex-start;
  font-size: 3vh;  width:40vh;
  }
    .control{
      display:-ms-flexbox;
      display:flex;
      -ms-flex-direction:column;
          flex-direction:column;
      -ms-flex-pack:distribute;
          justify-content:space-around;
    }
      #time-left{
        font-size:5vh;
        font-family: 'Orbitron', sans-serif;

      }
      #timer-label{
        font-size:5vh;
      }
.bottom{
  margin-bottom:25vh;
}
.buttons{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:justify;
      justify-content:space-between;
}

.btn{
  cursor:pointer;
  height:5vh;
  width: 5vw;
  border-radius: 50px;
  outline:none;
  border:none;
  background-color: rgba(179, 220, 229, 1);
  font-weight: bolder;
  text-align:center;
  font-size:2vh;
}
.btn:hover{
  background-color: rgba(179, 220, 229, 0.5);
  -webkit-box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
          box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



